.Menu {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
    z-index: 100;
}

.Button {
    padding: 40px 80px;
    font-size: 2em;
    border-radius: 20px;
    border: none;
    box-shadow: rgba(0, 0, 0, .5) 0px 0px 60px;
    cursor: pointer;
}

.Button:hover {
    background: rgb(44, 199, 62);
    color: white;
    transition: .3s all ease;
}